import Home from '@/views/Home'
export default [
	{
		path: '/',
		name: 'Home',
		component: Home,
		meta: { title: '首页', icon: 'home-o' },
	},
	{
		path: '/Detail',
		name: 'Detail',
		component: () => import('@/views/Detail'),
		meta: { title: '详情' },
	},
	{
		path: '/Login',
		name: 'Login',
		component: () => import('@/views/Login'),
		meta: { title: '登录' },
	},
	{
		path: '/Register',
		name: 'Register',
		component: () => import('@/views/Register'),
		meta: { title: '注册' },
	},
	{
		path: '/Discover',
		name: 'Discover',
		component: () => import('@/views/Discover'),
		meta: { title: '发现', icon: 'bulb-o' },
	},
	{
		path: '/Cart',
		name: 'Cart',
		component: () => import('@/views/Cart'),
		meta: { title: '购物车', icon: 'cart-circle-o' },
	},
	{
		path: '/Mine',
		name: 'Mine',
		component: () => import('@/views/Mine'),
		meta: { title: '我的', icon: 'https://b.yzcdn.cn/vant/icon-demo-1126.png' },
	},
	{
		path: '/Chart',
		name: 'Chart',
		component: () => import('@/views/Chart'),
		meta: { title: '图表' },
	},
]
