<template>
  <van-tabbar route :placeholder="true">
    <van-tabbar-item
        v-for="(route, index) in routes"
        :key="index"
        :to="route.path"
        :icon="route.meta.icon"
    >
      {{ route.meta.title }}
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
import routes from '@/router/routes'
export default {
  name: 'BaseTabbar',
  computed: {
    //返回菜单路由
    routes() {
      const menuRouteNameArr = ['Home', 'Cart', 'Discover', 'Mine']
      return routes.filter(item => menuRouteNameArr.includes(item.name))
    }
  }
}
</script>

