<template>
  <van-nav-bar
      :fixed="true"
      :placeholder="true"
      :safe-area-inset-top="true"
      :left-arrow="showBackPage"
      @click-left="onClickLeft"
  >
    <template #title>
      {{ $route.meta.title }}
    </template>
    <template #right>
      <van-button v-if="$route.name === 'Home' && isLogin"
                  @click="$router.push('/Login')"
                  type="info"
                  size="small"
                  round>去登录</van-button>
      <van-button v-if="$route.name === 'Login'"
                  @click="$router.push('/Register')"
                  type="info"
                  size="small"
                  round>去注册</van-button>
      <van-button v-if="$route.name === 'Detail'"
                  @click="addToCart"
                  type="danger"
                  size="small"
                  round>加入购物车</van-button>
      <van-button v-if="$route.name === 'Mine'"
                  @click="logout"
                  type="danger"
                  size="small"
                  round>退出登录</van-button>
    </template>
  </van-nav-bar>
</template>

<script>

export default {
  name: 'BaseNavBar',
  data() {
    return {
      isLogin: false
    }
  },
  computed: {
    showBackPage() {
      const noShowRouteArr = ['Detail', 'Login', 'Register', 'Chart']
      return noShowRouteArr.includes(this.$route.name)
    },
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1)
    },
    addToCart() {
      //如果没有登录进行提示，并跳转登录页
      if(!localStorage.getItem('token')) {
        this.$toast.fail('请登录后加入购物车')
        this.$router.push('/Login')
        return
      }
      const cartProductsList = JSON.parse(localStorage.getItem('cartProductsList')) || []
      const index = cartProductsList.findIndex(item => item.id === this.$route.query.id)
      if(index === -1 && this.$route.query.id) {
        cartProductsList.push({
          id: this.$route.query.id,
          count: 1,
        })
        this.$toast.success('添加购物车成功！')
      } else {
        this.$toast.fail('清不要重复添加购物车')
      }
      localStorage.setItem('cartProductsList', JSON.stringify(cartProductsList))
    },
    logout() {
      localStorage.clear()
      this.$toast('退出登录成功，稍后跳转首页')
      this.$router.replace('/')
    }
  },
  mounted() {
    this.isLogin = !localStorage.getItem('token')
  },
  updated() {
    this.isLogin = !localStorage.getItem('token')
  },
}
</script>

