<template>
  <div class="home-box">
    <van-search v-model="keyword" placeholder="请输入商品名称" @search="searchProducts" />
    <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="loadProducts"
        :offset="100"
    >
      <div class="products-item"
           v-for="item in products"
           :key="item.id"
      >
        <div class="item-left">
          <van-image
              width="100"
              height="100"
              lazy-load
              :src="item.coverImg"
          >
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
        <div class="item-right">
          <h3 class="name">{{ item.name }}</h3>
          <p class="quantity">数量：{{ item.quantity }}</p>
          <p class="price">价格：{{ (item.price / 100).toFixed(2) }}</p>
          <p class="describe">{{item.descriptions}}</p>
          <van-button type="info"
                      class="toDetail"
                      @click="toDetail(item._id)"
                      size="small"
                      round
          >
            查看详情
          </van-button>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import {reqProducts} from '@/api'

export default {
  name: 'Home',
  data() {
    return {
      products: [],
      keyword: '',
      loading: false,
      finished: false,
      page: 1,
      proId: 1,
    }
  },
  methods: {
    //搜索的时候，页面初始化到1
    searchProducts() {
      this.proId = 1
      reqProducts({
        name: this.keyword,
        page: this.page = 1,
      }).then(res => {
        for(let index in res.products) {
          res.products.id = this.proId++
        }
        this.products = res.products
      })
    },
    //加载时，当前页面加一
    loadProducts() {
      reqProducts({
        name: this.keyword,
        page: this.page++,
      }).then(res => {
        if(res.products.length) {
          for(let index in res.products) {
            res.products.id = this.proId++
          }
          this.products.push(...res.products)
          this.loading = false
        } else {
          this.finished = true
          this.$toast('所有商品已加载完毕')
        }
      })
    },
    //编程式导航，去详情页
    toDetail(id) {
      this.$router.push({
        path: `/Detail`,
        query: {
          id,
        },
      })
    }
  }
}
</script>

<style lang="less" scoped>
.products-item {
  position: relative;
  display: flex;
  justify-content: start;
  padding: 10px;

  &:active {
    background: #eee;
  }
  .item-right {
    overflow: hidden;
    padding-left: 10px;
    .name {
      font-size: 14px;
      margin: 0 0 6px;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      min-height: 30px;
    }
    .quantity, .price, .describe {
      font-size: 12px;
      padding-bottom: 4px;
      margin: 0;
    }
    .price {
      font-weight: bold;
      color: #f33;
    }
    .toDetail {
      position: absolute;
      right: 20px;
      bottom: 40px;
    }
    .describe {
      word-break: break-all;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>
