import Vue from 'vue'
import {
	Button, NavBar, Tabbar, TabbarItem, Toast, Search, List, Cell, Image as VanImage, Divider, Lazyload,
	Loading, Swipe, SwipeItem, Checkbox, CheckboxGroup, Form, Field, Card, Tag, Dialog, SubmitBar, Grid, GridItem
} from 'vant'

let useVantComponents = [
	Button, NavBar, Tabbar, TabbarItem, Toast, Search, List, Cell, VanImage, Divider, Lazyload,
	Loading, Swipe, SwipeItem, Checkbox, CheckboxGroup, Form, Field, Card, Tag, Dialog, SubmitBar, Grid, GridItem
]

export default function () {
	useVantComponents.forEach(item => {
		Vue.use(item)
	})
}
