<template>
  <div id="app">
    <BaseNavBar/>
    <RouterView/>
    <BaseTabbar v-if="showTabbar"/>
  </div>
</template>

<script>

import BaseNavBar from '@/components/BaseNavBar'
import BaseTabbar from '@/components/BaseTabbar'

export default {
  name: 'App',
  components: {
    BaseNavBar,
    BaseTabbar,
  },
  computed: {
    showTabbar() {
      const noShowRouteArr = ['Detail']
      return !noShowRouteArr.includes(this.$route.name)
    }
  }
}
</script>

<style>
html, body {
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
}
</style>
