import requests from '@/api/requests'

//请求所有商品
export const reqProducts = params => requests({
	url: '/products',
	method: 'get',
	params
})
//请求商品详情
export const reqProductsDetail = id => requests({
	url: `/products/${id}`,
	method: 'get',
})

//登录
export const reqLogin = data => requests({
	url: '/auth/login',
	method: 'post',
	data,
})

//注册
export const reqRegister = data => requests({
	url: '/auth/reg',
	method: 'post',
	data,
})
